<template>
  <div></div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'playLink',
  data () {
    return {}
  },
  computed: {
    ...mapState(['isShowAd'])
  },
  created () {
    const query = {
      id: this.$route.query.id
    }
    this.$router.replace({
      path: '/playVideo',
      query: query
    })
  }
}
</script>



