<template>
  <bonus-dialog>
    <template v-slot:title>
      <div
        v-for="desc of missionDescription"
        :key="desc">
        {{ desc }}
      </div>
    </template>
    <div class="flex row no-wrap justify-center">
      <div
        class="bonusDialogWait_btn bonusDialogWait_btn_watch"
        @click="$emit('handle-continue-watch', 'WAIT')">继续看</div>
      <div class="bonusDialogWait_btn bonusDialogWait_btn_promo">
        <div
          class="bonusDialogWait_btn_promo_link"
          @click="handleOpenLink">{{bonusInfo.btnText}}</div>
      </div>
    </div>
  </bonus-dialog>
</template>

<script>
import BonusDialog from './BonusDialog'
import { missionType, xbbLogin } from '../utils'
export default {
  name: 'BonusDialogWait',
  components: {
    BonusDialog
  },
  props: {
    mission: {
      type: Object,
      default: null
    }
  },
  computed: {
    missionDescription () {
      if (this.mission && this.mission.Description) {
        return this.mission.Description.split(' ')
      }

      return ['请查看推广赚钱', '即可继续享有观影送钱！']
    },
    bonusInfo () {
      const { ActionType: actionType } = this.mission || {}
      const { cid, userid, domain, promoUrl } = this.$store.state.xbbBonus
      let result = null

      switch (actionType) {
        case missionType.view:
          result = {
            btnText: '去查看',
            XBB_PROMO_URL: `${promoUrl}?check=true&cid=${cid}&userid=${userid}&tagId=${this.mission && this.mission.TagId}&domain=${domain}`
          }
          break
        case missionType.guest:
          result = {
            btnText: '去查看',
            XBB_PROMO_URL: null
          }
          break
        default:
          result = {
            btnText: '去推广',
            XBB_PROMO_URL: `${promoUrl}?check=false&cid=${cid}&userid=${userid}&tagId=${this.mission && this.mission.TagId}&domain=${domain}`
          }
          break
      }
      return result
    }
  },
  methods: {
    /**
     * 因為ios app 當頁開啟再返回後卻沒有重整，導致彈窗沒被關閉，所以使用這方式強迫關閉
     */
    handleOpenLink () {
      const { ActionType: actionType } = this.mission || {}
      this.$emit('close-dialog')
      if (actionType === missionType.guest) {
        xbbLogin()
      } else {
        window.location.href = this.bonusInfo.XBB_PROMO_URL
      }
    }
  }
}
</script>

<style scoped>
.bonusDialogWait_btn {
  width: calc(100% - 50px);
  height: 40px;
  margin: 20px 10px 0 10px;
  font-size: 15px;
  font-weight: 700;
  line-height: 40px;
  text-align: center;
  border: none;
  border-radius: 20px;
}

.bonusDialogWait_btn_watch {
  color: #be9e7f;
  background-color: #faf5f0;
}

.bonusDialogWait_btn_promo {
  background: linear-gradient(270deg, #bd9d7d, #f9ddbd);
}

.bonusDialogWait_btn_promo_link {
  display: block;
  width: 100%;
  height: 100%;
  color: #fff;
  text-decoration: none;
}

.flex {
  display: flex;
}

.row {
  display: flex;
}

.no-wrap {
  flex-wrap: nowrap;
}

.justify-center {
  justify-content: center;
}
</style>



