<template>
  <div class="search-content">
    <div
      v-if="$route.name !== 'tags'"
      class="search-nav">
      <div
        class="search-nav-back"
        @click="back()">
        <img src="../assets/images/nav/back1.png" />
      </div>
      <h4>{{ pageTitle }}</h4>
      <div
        class="search-nav-search"
        @click="itemFun()">
        <img src="../assets/images/home/search.png" />
      </div>
    </div>
    <div class="navbar-content">
      <div class="sg-tag">
        <span
          v-for="(item, index) in videoSortList"
          :key="index"
          :class="{ tasActive : queryData.orderby === item.value }"
          class="sg-tag-item"
          @click="sortHandler(item.value)">{{item.title}}</span>
      </div>
      <van-tabs
        v-model="videoTypeId"
        type="card"
        title-inactive-color="#fff"
        title-active-color
        background="#111111"
        animate
        swipeable
        @click="typeHandler">
        <van-tab
          v-for="item in videoTypeList"
          :key="item.id"
          :title="item.name"
          :name="item.id"></van-tab>
      </van-tabs>
    </div>
    <refresh-list
      class="search-content-list"
      :refresh-loading.sync="refreshLoading"
      :list-loading.sync="listLoading"
      :finished="finished"
      :error.sync="error"
      @refresh="onRefresh"
      @load="onLoad">
      <ul class="search-content-list-view">
        <li
          v-for="item in videoList"
          :key="item.id"
          class="search-content-list-view-cell"
          @click="liChange(item.id)">
          <div class="search-content-list-logo">
            <base-image
              class="search-content-list-logo-img"
              fit="cover"
              encrypt
              :url="item.coverbase64.url"
              :type="item.coverbase64.type"/>
          </div>
          <p class="search-content-list-title">{{item.title}}</p>
        </li>
      </ul>
    </refresh-list>
  </div>
</template>
<script>
import { selectItemList } from '@/API/home'
import { selectList } from '@/API/list'
import baseImage from '@/components/baseImage'
import refreshList from '@/components/refreshList'

export default {
  name: 'list',
  components: {
    baseImage,
    refreshList
  },
  data () {
    return {
      videoSortList: [
        { title: '全部', value: '' },
        { title: '最新', value: 'new' },
        { title: '最热', value: 'hot' },
        { title: '最多喜欢', value: 'like' }
      ],
      videoTypeList: [],
      videoList: [],
      videoTypeId: 0,
      queryData: {
        orderby: '',
        page: 1
      },
      refreshLoading: false,
      listLoading: false,
      finished: false,
      error: false
    }
  },
  computed: {
    pageTitle () {
      const typeData = this.videoTypeList.filter(type => type.id === this.videoTypeId)
      return typeData[0] ? typeData[0].name : ''
    }
  },
  async created () {
    this.videoTypeId = +this.$route.query.typeId || 0
    this.queryData.orderby = this.$route.query.orderby || ''
    await this.getVideoTypeList()
  },
  methods: {
    async onLoad () {
      await this.getVideoList()
    },
    async onRefresh () {
      this.videoList = []
      this.queryData.page = 1
      await this.getVideoList()
      this.$toast('刷新成功')
      this.refreshLoading = false
    },
    replaceQueryString (key, value) {
      const { name, query } = this.$route
      this.$router.replace({
        name,
        query: {
          ...query,
          [key]: value
        }
      })
    },
    async typeHandler (id) {
      this.replaceQueryString('typeId', id)
      await this.onRefresh()
    },
    async sortHandler (value) {
      this.queryData.orderby = value
      this.replaceQueryString('orderby', value)
      await this.onRefresh()
    },
    async getVideoTypeList () {
      await selectItemList()
        .then(res => {
          this.videoTypeList = [
            {
              id: 0,
              name: '全部',
              icopath: null,
              order: 0,
              icobase64: {
                url: null,
                type: null
              }
            },
            ...res.rescont
          ]
        })
        .catch(err => {
          console.log(err)
        })
    },
    async getVideoList () {
      try {
        const res = await selectList({ type: this.videoTypeId, params: this.queryData })
        const { data, next_page_url: nextPageUrl } = res.rescont
        this.videoList = this.queryData.page === 1 ? data : this.videoList.concat(data)
        this.queryData.page = this.queryData.page + 1
        if (nextPageUrl === null) {
          this.listLoading = false
          this.finished = true
          return
        }
      } catch (err) {
        console.log(err)
        this.error = true
      }
      this.finished = false
      this.listLoading = false
    },
    itemFun () {
      this.$router.push({ path: '/search' })
    },
    liChange (id) {
      this.$router.push({
        path: '/playVideo',
        query: {
          id
        }
      })
    },
    back () {
    //  this.$router.go(-1); //返回上壹層

      this.$router.push('/home')
    }
  }
}
</script>
<style>
.van-tabs--card > .van-tabs__wrap {
  box-sizing: border-box;
  height: 44px !important;
  padding: 7px 0 !important;
}

.van-tabs__nav--card {
  box-sizing: border-box;
  height: 26px !important;
  margin: 0 5px;
  border: 0 solid #ee0a24 !important;
  border-radius: 2px;
}

.van-tabs__nav--card .van-tab {
  line-height: 24px;
  border-right: 0 solid #ee0a24 !important;
}

.van-tabs__nav--card .van-tab.van-tab--active {
  color: #fff !important;
  background-color: #ff5c38 !important;
  border-radius: 16px;
}

.tasActive {
  background-color: #ff5c38 !important;
}

.sg-tag {
  position: relative;
  box-sizing: border-box;
  display: inline-flex;
  justify-content: left;
  width: 100%;
  height: 44px;
  padding: 10px 5px;
}

.sg-tag-item {
  box-sizing: border-box;
  height: 24px;
  padding: 2px 10px;
  margin-left: 10px;
  font-size: 13px;
  line-height: 20px;
  color: #fff;
  text-align: center;
  background-color: #444;
  border-radius: 15px;
}

.search-content {
  /* min-height: 100vh;
  background-color: #1d1d1d;
  position: relative; */
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.search-nav {
  position: absolute top：0px;
  right: 0;
  left: 0;
  box-sizing: border-box;
  width: 100%;
  height: 44px;
  padding: 0 40px;
  font-size: 18px;
  font-weight: 600;
  line-height: 44px;
  color: #fff;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.search-nav h4 {
  width: 100%;
  height: 44px;
  padding: 0 0;
  margin: 0 0;
  font-weight: 600;
  line-height: 44px;
  color: #fff;
}

.search-nav-back {
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 40px;
  height: 44px;
  padding: 5px 10px;
}

.search-nav-back img {
  width: 24px;
  height: 24px;
}

.search-nav-search {
  position: absolute;
  top: 0;
  right: 0;
  box-sizing: border-box;
  width: 40px;
  height: 44px;
  padding: 5px 10px;
}

.search-nav-search img {
  width: 24px;
  height: 24px;
}

.navbar-content {
  position: relative;
  width: 100%;
  height: 90px;
  background-color: #111;
}

.search-content-list {
  /* position: absolute;
  overflow: auto;
  z-index: 98;
  top: 135px;
  left: 0px;
  right: 0px;
  bottom: 10px;
   -ms-overflow-style: none;
   overflow: -moz-scrollbars-none; */
  flex: 1;
  width: 100%;
  overflow: auto;
}
.search-content-list::-webkit-scrollbar { display: none; }

.search-content-list-view {
  position: relative;
  box-sizing: border-box;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  padding: 10px 4vw 0 4vw;
  margin: 0 0;
  list-style: none;
}

.search-content-list-view-cell {
  position: relative;
  width: 44vw;
  margin-bottom: 10px;
}

.search-content-list-logo {
  position: relative;
  width: 100%;
  height: 27vw;
}

.search-content-list-logo-img {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 4px;
}

.search-content-list-title {
  width: 100%;
  margin: 0 0;
  overflow: hidden;
  font-size: 12px;
  font-weight: 400;
  line-height: 30px;
  color: rgba(255, 255, 255, 1);
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



