export const throttle = function (fn, gap = 1000) {
  let lastTime = 0
  return function (...args) {
    const now = new Date().getTime()
    if (now - lastTime > gap) {
      lastTime = now
      fn.apply(this, args)
    }
  }
}

export const debounce = function (fn, wait = 1000) {
  let timer = null
  return function (...args) {
    if (timer) {
      clearTimeout(timer)
      timer = null
    } else {
      fn.apply(this, args)
    }
    timer = setTimeout(function () {
      fn.apply(this, args)
    }, wait)
  }
}

export const format = function (date, fmt) {
  const o = {
    'M+': date.getMonth() + 1, // 月
    'd+': date.getDate(), // 日
    'h+': date.getHours(), // 时
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    S: date.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  for (const k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1
          ? o[k]
          : ('00' + o[k]).substr(('' + o[k]).length)
      )
    }
  }
  return fmt
}

export function timeRender (time) {
  const t = Math.round(new Date(time.replace(/-/g, '/')).getTime() / 1000)
  const now = Math.round(new Date().getTime() / 1000)
  const today = new Date(new Date().setHours(0, 0, 0, 0)) / 1000
  const diffTime = now - t

  if (diffTime < 60) {
    return diffTime <= 0 ? '刚刚' : diffTime + '秒前'
  } else if (diffTime < 60 * 60) {
    return Math.round(diffTime / 60) + '分钟前'
  } else if (t > today) {
    return Math.round(diffTime / 60 / 60) + '小时前'
  } else if (t > today - 60 * 60 * 24) {
    return '昨天' + format(new Date(t * 1000), 'hh:mm')
  } else if (t > today - 60 * 60 * 24 * 2) {
    return '前天' + format(new Date(t * 1000), 'hh:mm')
  } else {
    return format(new Date(t * 1000), 'yyyy-MM-dd hh:mm:ss')
  }
}

export const isIOS = (/iPad|iPhone|iPod/.test(navigator.platform) ||
  (navigator.platform === 'MacIntel' && navigator.maxTouchPoints > 1)) &&
  !window.MSStream

/**
 * Fisher-Yates shuffle
 */
export const shuffle = (arr = []) => {
  const newArr = [...arr]
  const n = arr.length
  for (let i = 0; i < n; i += 1) {
    const rand = Math.floor(Math.random() * n);
    [newArr[i], newArr[rand]] = [newArr[rand], newArr[i]]
  }
  return newArr
}



