<template>
  <div>
    <swiper
      ref="mySwiper"
      :options="swiperOption">
      <swiper-slide
        v-for="(image, index) in images"
        :key="index">
        <a
          :href="image.url"
          target="_blank">
          <base-image
            class="swiper-img"
            :url="image.imgbase64.url"
            :type="image.imgbase64.type"
            encrypt
            :lazy="false"/>
        </a>
      </swiper-slide>
      <div
        v-if="swiperOption.pagination"
        class="swiper-pagination"
        slot="pagination"></div>
    </swiper>
  </div>
</template>
<script>
import '../assets/css/swiper.min.css'
import baseImage from '@/components/baseImage'
export default {
  name: 'banner',
  components: {
    baseImage
  },
  props: { images: Array, swiperOption: null },
  mounted () {
  }
}
</script>
<style>
.swiper-img {
  width: 100%;
  height: 145px;
  overflow: hidden;
  border-radius: 10px;
}

.swiper-inner {
  width: 100%;
  height: 220px;
  padding-top: 50px;
  padding-bottom: 50px;
  border-radius: 10px;
}

.swiper-slide {
  width: 315px !important;
  background-position: center;
  background-size: cover;
  border-radius: 10px;
}

.swiper-container-v {
  background: #eee;
}

.swiper-container-v .swiper-slide {
  width: 100%;
  background: #eee;
}

.swiper-pagination-bullet {
  background: #ddd;
  opacity: 0.5;
}

.swiper-pagination-bullet-active {
  width: 16px;
  background: #fff !important;
  border-radius: 4px;
  opacity: 1;
}
</style>



