<template>
  <component
    :is="rootComponent"
    class="baseImg"
    @show="handleImage">
    <van-image
      :fit="fit"
      :width="width"
      :height="height"
      :src="image"
      :show-loading="showLoading"
      :round="round"
      v-on="$listeners">
      <template v-slot:loading>
        <img
          class="exImg"
          :src="require('../assets/images/home/loading.gif')"/>
      </template>
      <template v-slot:error>
        <img
          class="exImg"
          :src="require('../assets/images/home/video-loading.png')"/>
      </template>
    </van-image>
  </component>
</template>

<script>
import { imgToBase64 } from '../API/common'

export default {
  name: 'baseImage',
  components: {
    emptyComponent: {
      template: '<div><slot/></div>',
      mounted () {
        this.$emit('show')
      }
    }
  },
  props: {
    type: String,
    url: String,
    encrypt: {
      type: Boolean,
      default: false
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '100%'
    },
    fit: {
      type: String
    },
    showLoading: {
      type: Boolean,
      default: true
    },
    round: {
      type: Boolean,
      default: false
    },
    lazy: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      image: null
    }
  },
  computed: {
    rootComponent () {
      return this.lazy ? 'lazy-component' : 'empty-component'
    }
  },
  watch: {
    url (newUrl, oldUrl) {
      this.handleImage()
    }
  },
  methods: {
    handleImage () {
      if (this.encrypt) {
        if (this.url === null) {
          console.warn('[Tip]: Prop "url" is "null". It is recommended to use "undefined" in `encrypt` mode for initial component.')
          this.image = ''
        } else if (this.url) {
          imgToBase64(this.url, this.type).then(res => {
            this.image = res
          })
        }
      } else {
        this.image = this.url
      }
    }
  }
}
</script>

<style scoped>
.exImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.baseImg >>> .van-image__loading,
.baseImg >>> .van-image__error {
  background-color: transparent;
}
</style>



