import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
Vue.use(Vuex)

// store 持久化儲存
const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
  reducer: state => ({
    xbbBonus: state.xbbBonus
  })
})

const store = new Vuex.Store({
  plugins: [vuexLocal.plugin],
  state: {
    tab: {
      image: 'sg/activity/2020/07/10/159436862710000.png',
      image_gray: 'sg/activity/2020/07/10/15943686349999.png',
      name: 'vip模块网址',
      open_type: 0,
      url: ''
    },
    cueTimes: '0',
    isShowAd: window.__CONFIG__.VUE_APP_AD, // 是否展示广告
    xbbBonus: {
      active: false,
      domain: '',
      userid: '',
      cid: '',
      wsUrl: '',
      promoUrl: ''
    },
    appMoneyLink: ''
  },
  mutations: {
    setTab (state, data) {
      state.tab = Object.assign(state.tab, data)
    },
    setCueTimes (state, data) {
      state.cueTimes = data
    },
    setAmount (state, data) {
      state.Amount = data
    },
    setTotalAmount (state, data) {
      state.TotalAmount = data
    },
    setXbbBonus (state, { isGuest, domain, userid, cid, ws_url: wsUrl, promo_url: promoUrl }) {
      if (isGuest) {
        state.xbbBonus = {
          active: false,
          domain: '',
          userid: '',
          cid: '',
          wsUrl: '',
          promoUrl: ''
        }
      } else {
        state.xbbBonus = {
          active: true,
          domain,
          userid,
          cid,
          wsUrl,
          promoUrl
        }
      }
    },
    setAppMoneyLink (state, link) {
      state.appMoneyLink = link
    }
  },
  actions: {},
  getters: {
    tab: state => state.tab,
    cueTimes: state => state.cueTimes,
    isShowAd: state => state.isShowAd
  }
})

export default store



