<template>
  <bonus-dialog>
    <template v-slot:title>
      <div>请推广好友充值</div>
      <div> 领取更多礼金 享受每月分红</div>
    </template>
    <div class="flex row no-wrap">
      <div
        class="bonusDialogFull_btn bonusDialogFull_btn_watch"
        @click="$emit('handle-continue-watch')">继续看</div>
      <div class="bonusDialogFull_btn bonusDialogFull_btn_promo">
        <div
          class="bonusDialogFull_btn_promo_link"
          @click="handleOpenLink">去推广</div>
      </div>
    </div>
  </bonus-dialog>
</template>

<script>
import BonusDialog from './BonusDialog'
export default {
  name: 'bonusDialogFull',
  components: {
    BonusDialog
  },
  computed: {
    XBB_PROMO_URL () {
      const { cid, userid, domain, promoUrl } = this.$store.state.xbbBonus

      return `${promoUrl}?check=false&cid=${cid}&userid=${userid}&domain=${domain}`
    }
  },
  methods: {
    /**
     * 因為ios app 當頁開啟再返回後卻沒有重整，導致彈窗沒被關閉，所以使用這方式強迫關閉
     */
    handleOpenLink () {
      this.$emit('close-dialog')
      window.location.href = this.XBB_PROMO_URL
    }
  }
}
</script>

<style  scoped>
.bonusDialogFull_btn {
  width: calc(100% - 50px);
  height: 40px;
  margin: 20px 10px 0 10px;
  font-size: 15px;
  font-weight: 700;
  line-height: 40px;
  text-align: center;
  border: none;
  border-radius: 20px;
}

.bonusDialogFull_btn_watch {
  color: #be9e7f;
  background-color: #faf5f0;
}

.bonusDialogFull_btn_promo {
  background: linear-gradient(270deg, #bd9d7d, #f9ddbd);
}

.bonusDialogFull_btn_promo_link {
  display: block;
  width: 100%;
  height: 100%;
  color: #fff;
  text-decoration: none;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.no-wrap {
  flex-wrap: nowrap;
}

.row {
  flex-direction: row;
}
</style>



