import Vue from 'vue'
import Router from 'vue-router'
import home from '@/views/home'
import mine from '@/views/mine'
import search from '@/components/search'
import list from '@/components/list'
import playVideo from '@/components/playVideo'
import searchList from '@/components/searchList'
import playLink from '@/components/playLink'
import find from '@/views/find/find'
Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      component: home
    },
    {
      path: '/vendor',
      name: 'vendor',
      component: () => import('@/views/vendor')
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/find',
      name: 'find',
      component: find,
      children: [
        {
          path: 'artist',
          name: 'artist',
          component: () => import('@/views/find/artist')
        },
        {
          path: 'rank',
          name: 'rank',
          component: () => import('@/views/find/rank')
        },
        {
          path: 'tags',
          name: 'tags',
          component: () => import('@/views/find/tags')
        }
      ]
    },
    {
      path: '/artist-details',
      name: 'artist-details',
      component: () => import('@/views/find/artist-details')
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine
    }, {
      path: '/search',
      name: 'search',
      component: search
    }, {
      path: '/list',
      name: 'list',
      component: list

    }, {
      path: '/playVideo',
      name: 'playVideo',

      component: playVideo
    }, {
      path: '/searchList',
      name: 'searchList',
      component: searchList
    }, {
      path: '/playLink',
      name: '/playLink',
      component: playLink
    }

  ]
})

router.afterEach((to, from, next) => {
  const contentUrl = to.path
  const refererUrl = from.path
  if (refererUrl !== contentUrl && window._czc) {
    window._czc.push(['_trackPageview', contentUrl, refererUrl])
  }
})
export default router



