<template>
  <div class="sg-tabbar">
    <div
      class="sg-tabbar-item"
      @click="tabbarChange(0)">
      <img
        v-if="$route.path.indexOf('/home') === 0"
        src="../assets/images/tabbar/homeActive.png" />
      <img
        v-else
        src="../assets/images/tabbar/home.png" />
      <p :class="{ active : $route.path.indexOf('/home') === 0}">首页</p>
    </div>
    <div
      class="sg-tabbar-item"
      @click="tabbarChange(2)">
      <img
        v-if="$route.path.indexOf('/find') === 0"
        src="../assets/images/tabbar/findActive.png" />
      <img
        v-else
        src="../assets/images/tabbar/find.png" />
      <p :class="{ active : $route.path.indexOf('/find') === 0}">发现</p>
    </div>
    <div
      class="sg-tabbar-item"
      @click="tabbarChange(3)">
      <img
        v-if=" $route.path.indexOf('/mine') === 0"
        src="../assets/images/tabbar/mineActive.png" />
      <img
        v-else
        src="../assets/images/tabbar/mine.png" />
      <p :class="{ active : $route.path.indexOf('/mine') === 0}">我的</p>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  name: 'tabbar',
  data () {
    return {
      tabbarIndex: 0,
      pathList: ['/home', '/vip', '/find/rank', '/mine']
    }
  },
  computed: {
    ...mapGetters([
      'tab'
    ])
  },
  methods: {
    tabbarChange (index) {
      this.$router.push({ path: this.pathList[index] })
    }
  }
}
</script>
<style scoped>
.sg-tabbar {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 9999;
  box-sizing: border-box;
  display: inline-flex;
  justify-content: space-around;
  width: 100%;
  height: 50px;
  padding: 5px 0;
  background-color: rgba(0, 0, 0, 0.9);
  border-top: 1px solid #999;
}

.sg-tabbar-item {
  position: relative;
  width: 40px;
  text-align: center;
}

.sg-tabbar-item img {
  width: 22px;
  height: 22px;
}

.sg-tabbar-item p {
  width: 100%;
  padding: 0 0;
  margin: 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  color: rgba(153, 153, 153, 1);
  text-align: center;
}

.sg-tabbar .sg-tabbar-item .active {
  color: #ff5c38 !important;
}
</style>



