import request from '@/utils/request'

export function selectggList () {
  return request({
    url: '/api/adinfo/USER',
    method: 'get'
  })
}

export function checkinUrl () {
  return request({
    url: '/api/applink/checkin',
    method: 'get'
  })
}
export function sactivityUrl () {
  return request({
    url: '/api/applink/activity',
    method: 'get'
  })
}



