<script>
import videojs from 'video.js'
import 'video.js/dist/video-js.css'
import { checkinUrl, sactivityUrl } from '@/API/mine'
import { imgToBase64 } from '../API/common'
import { searchList } from '@/API/search'
import $ from 'jquery'
import baseImage from '@/components/baseImage'
import {
  selectVideoInfo,
  getLikeList,
  playggimg,
  conterggimg
} from '@/API/playVideo'
import Xbb from '@/views/xbb/Index'
import Fake from '@/views/xbb/Fake'
import 'videojs-overlay'
import { mapMutations, mapState } from 'vuex'

export default {
  name: 'playVideo',
  components: {
    Xbb,
    Fake,
    baseImage
  },
  data () {
    return {
      //  flag:false,
      comment: [],
      videoinfo: {},
      videoId: '',
      ad: {},
      tagsHeight: 0,
      playimg: null,
      conterimg: null,
      maylike: [],
      ggBool: false,
      timeNum: 5,
      closeTitle: '',
      queryData: {
        page: 1
      },
      likeCount: 0,
      likeItem: false,
      checkin: '',
      sactivity: '',
      tagsTitle: '',
      tagqueryData: {
        serach: '',
        page: 0
      },
      tageListVideo: [],
      progress: 0,
      pause: true,
      progressPause: false,
      showGoldWarp: false,
      goldWarpType: 'WAIT',
      goldWarpLastTime: new Date().getTime() + 5000,
      isseeking: false,
      setTimeoutOnpause: null,
      messionActionType: null,
      messionDescription: null,
      // 是否关闭广告我的变量
      Description: false, // 弹窗句子
      CloseAd: false,
      countOne: 0,
      player: null
    }
  },
  computed: {
    ...mapState(['isShowAd', 'Judgment', 'xbbBonus']),
    playerSource () {
      return {
        src: this.videoinfo.videopath || '',
        type: 'application/vnd.apple.mpegurl'
      }
    }
  },
  created () {
    this.ggBool = true
    this.closeTitle = this.timeNum + 'S'
    this.videoId = this.$route.query.id
    sessionStorage.setItem('videoId', this.videoId)
    // 拿参数
    if (!this.$store.getters.isShowAd) {
      this.CloseAd = true
    }
    this.getVideoInfo(() => {
      this.setTimeFun()
    })
    this.getPlayggImg()
    this.getconterggimg()
    this.getcheckinUrl()
    this.getsactivityUrl()
    this.getLikeListFun()
  },
  mounted () {
    this.init()
  },
  methods: {
    ...mapMutations(['setJudgment']),
    init () {
      this.player = videojs(this.$refs.videoPlayer, {
        // 确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        controls: true,
        // 自动播放属性,muted:静音播放
        autoplay: false,
        // 建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        preload: 'auto',
        // 视频比例
        aspectRatio: '16:9',
        // 流体模式
        fluid: true,
        // //填充
        fill: true,
        // //响应式
        responsive: false,
        language: 'zh-CN',
        // 是否循环播放
        loop: true,
        // 播放前图片
        // poster: this.videoData.image,
        // 是否预加载
        // preload: "auto",
        // 阻止播放器为具有data-setup属性的媒体元素运行自动设置
        autoSetup: false,
        // 播放速度
        playbackRates: [1, 4, 8, 16],
        controlBar: {
          volumePanel: {
            inline: false
          }
        },
        html5: {
          hls: {
            cacheEncryptionKeys: true
          }
        }
      })

      this.player.addChild('Overlay', {
        start: 'ready',
        content: this.$refs.xbb
      })
      // 註冊播放事件
      this.player.on('playing', (e) => {
        this.playingEvent(e)
      })
      // 註冊暫停事件
      this.player.on('pause', (e) => {
        if (!this.player.seeking()) {
          this.pauseEvent(e)
        }
      })
    },
    playingEvent () {},
    pauseEvent () {},
    getVideoInfo (callback) {
      selectVideoInfo()
        .then((res) => {
          this.videoinfo = res.rescont
          callback()
        })
        .catch((err) => {
          console.log(err)
        })
    },
    getPlayggImg () {
      playggimg()
        .then((res) => {
          this.playimg = res.rescont[0]
        })
        .catch((err) => {
          console.log(err)
        })
    },
    getLikeListFun () {
      getLikeList(this.queryData)
        .then((res) => {
          for (let index = 0; index < res.rescont.data.length; index++) {
            this.maylike.push(res.rescont.data[index])
          }
          this.likeCount = res.rescont.last_page
          if (this.likeCount > 1) {
            this.likeItem = true
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    getconterggimg () {
      conterggimg()
        .then((res) => {
          this.conterimg = res.rescont[0]
        })
        .catch((err) => {
          console.log(err)
        })
    },
    jzItemChange () {
      if (this.queryData.page++ < this.likeCount) {
        this.getLikeListFun()
      } else {
        this.likeItem = false
      }
    },
    clearFun () {
      if (this.timeNum <= 0) {
        this.ggBool = false
      } else {
        this.ggBool = true
      }
    },
    getcheckinUrl () {
      checkinUrl()
        .then((res) => {
          this.checkin = res.rescont.url
        })
        .catch((err) => {
          console.log(err)
        })
    },
    getsactivityUrl () {
      sactivityUrl()
        .then((res) => {
          this.sactivity = res.rescont.url
        })
        .catch((err) => {
          console.log(err)
        })
    },
    setTimeFun () {
      const _this = this
      if (_this.CloseAd) {
        setTimeout(() => {
          _this.player.src(_this.playerSource)
          _this.player.load(_this.url)
        }, 1000)
      } else {
        const i = setInterval(function () {
          if (_this.timeNum > 0) {
            _this.timeNum--
            _this.closeTitle = _this.timeNum + 'S'
          } else {
            _this.player.src(_this.playerSource)
            _this.player.load(_this.url)
            _this.closeTitle = '关闭'
            window.clearInterval(i)
          }
        }, 1000)
      }
      imgToBase64(this.videoinfo.coverbase64.url, this.videoinfo.coverbase64.type).then(data => {
        this.player.poster(data)
      })
    },
    liChange (index) {
      const query = {
        id: this.maylike[index].id
      }
      this.$router.replace({
        path: '/playLink',
        query: query
      })
    },
    back () {
      this.$router.go(-1)
    },
    tagsItemChange (title, id) {
      this.tageListVideo = []
      this.tagsTitle = title
      this.tagqueryData.serach = title
      this.tagqueryData.page = 1
      $('#tagList').show()
      $('#tagList').animate({ top: $('#playVideo').height() }, 200)
      searchList(this.tagqueryData)
        .then((res) => {
          this.tageListVideo = res.rescont.data
        })
        .catch((err) => {
          console.log(err)
        })
    },
    tagsItemlose () {
      $('#tagList').hide()
    }
  }
}
</script>
