<template>
  <div class="bonusDialog z-top">
    <img
      src="../../../assets/images/xbb/coin_title.png"
      class="bonusDialog_icon"/>
    <div class="bonusDialog_title">
      <slot name="title"></slot>
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'BonusDialog'
}
</script>

<style scoped>
.bonusDialog {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  max-width: 560px;
  min-height: 150px;
  padding: 28px 12px 12px 12px;
  margin: auto;
  overflow: visible;
  text-align: center;
  background-color: #fff;
  border: 6px solid #ecc374;
  border-radius: 11px;
}

.bonusDialog_icon {
  position: absolute;
  top: 0;
  left: 50%;
  width: 140px;
  height: 100px;
  margin: -75px 0 0 -70px;
}

.bonusDialog_title {
  font-size: 18px;
  font-weight: 700;
  line-height: 1.5;
  color: #bf8646;
}

.z-top {
  z-index: 7000;
}
</style>



