import axios from 'axios'
import MD5 from 'md5.js'

let mDiffTime = 0

function StringifyUrlSearch (search = {}) {
  return (
    Object.entries(search)
      .reduce((t, v) => `${t}${v[0]}=${encodeURIComponent(v[1])}&`, '')
      .replace(/&$/, '')
  )
}

function buildUrl (url, key = 'KTak4PD30WQm8k0KS5M3RlWyZ1Owy$o%', token = 'Bearer') {
  let _url = url
  if (_url.includes('?')) {
    _url += '&uuid=0&device=0'
  } else {
    _url += '?uuid=0&device=0'
  }
  if (!_url.includes('time=')) {
    _url +=
    '&time=' + (Math.round(new Date().getTime() / 1000) + mDiffTime - 5)
  }
  const urlArr = _url
    .substr(url.indexOf('?') + 1)
    .split('&')
    .map(e => e.split('='))
    .reduce(
      (p, c) => {
        p[c[0]] = c[1]
        return p
      },
      { key, token: token.replace('Bearer ', '') }
    )
  const urlArrKey = Object.keys(urlArr)
  urlArrKey.sort()
  const urlArrSort = urlArrKey.reduce((p, c) => {
    p[c] = urlArr[c]
    return p
  }, {})
  const newUrl = StringifyUrlSearch(urlArrSort)
  const sign = new MD5().update(newUrl).digest('hex')
  // const sign = md5(newUrl).toString();
  const res = _url + '&signature=' + sign
  return res
}

const baseURL = process.env.NODE_ENV === 'development' ? '/' : window.__CONFIG__.VUE_APP_API_URL

const service = axios.create({
  baseURL,
  timeout: 60000, // 请求超时时间
  responseType: 'json',
  headers: {
    'Content-Type': 'application/json'
  },
  withCredentials: true, // 跨域携带cookie
  strictSSL: false,
  changeOrigin: true,
  rejectUnauthorized: true, // add when working with https sites
  requestCert: false, // add when working with https sites
  agent: false // add when working with https sites
})

// 添加请求拦截器，这里面可以配置一下每次请求都需要携带的参数，比如 token，timestamp等等，根据项目自己配置
service.interceptors.request.use(request => {
  // 处理v2签名
  if (request.url.includes('/v2')) {
    request.url = buildUrl(request.url)
  }
  return request
})
service.interceptors.response.use(
  response => {
    /**
       * 判断服务器请求是否成功
       * @method if
       * @param  {[type]} response [description]
       * @return {[type]}          [description]
       */
    if (response.data.code !== 200) {
      return Promise.reject(new Error('网络异常，请稍后重试'))
    }
    const res = response.data
    if (res.code >= 200 && res.code < 300) {
      return res
    }
  },
  error => {
    return Promise.reject(error)
  }
)
// 校验时间
service({ url: '/api/systemtime/' + Math.round(new Date().getTime() / 1000) })
  .then(result => {
    if (result && result.rescont && typeof result.rescont.time !== 'undefined') {
      mDiffTime = result.rescont.time
    }
  })
  .catch(err => console.warn(err))

export const normalRequest = axios.create()
export default service



